<template>
    <div id="vr">
        <h1>VSR Download</h1>
        <el-button class="dbtn" @click="download('win7.tar.gz')" type="primary">Windows 7+</el-button>
        <el-button class="dbtn" @click="download('apple.tar.gz')" type="primary">OSX</el-button>
    </div>
</template>

<script>
import Vue from 'vue';
export default {
  name:'vr',
  methods: {
    download: function (file){
        var url = '/static/'+file;
        console.log(url);
        this.$http.get(url).then(response=>{},response=>{});
    }
  }
}
</script>

<style>
#vr{
  padding:30px;
  margin:10%;
  width:80%;
  background-color:white;
  opacity:0.8;
}
</style>



