<template>
  <div id="pinboard">
    <!-- comments to publish -->
    <el-input
    type="textarea"
    :rows="2"
    minr
    placeholder="publish your comments here"
    :autosize="{minRows: 4}"
    v-model="userComment">
    </el-input>
    <el-button id="cbtn" type="success" @click="publishComments" plain>comment</el-button>
    <!-- others' comments -->
    <div class="comment" v-for="item in comments">
      <!-- No avatar is stored in backend currently -->
      <!-- <div class="ubox">
      <img class="avatar" :src="item.avatar" height="100px" width="100px" />
          <label class="uname">{{item.username}}</label>
      </div>-->
      <div class="content">
        <label class="ctime"><strong>{{item.uname}}</strong> commented at {{item.time}}</label>
        <p style="padding:5px 10px;">{{item.content}}</p> 
      </div> 
    </div>
  </div>
</template>

<script>
import Vue from 'vue'

export default {
  name: 'pinboard',
  props:['comments','url','sid'],
  data () {
    return{
        userComment:''
    }
  },
  methods:{
    publishComments: function(){
      var date = new Date();
      var publishDate = date.getFullYear() + '-' 
                        + (date.getMonth()+1) + '-' 
                        + date.getDate() + ' '
                        + date.getHours() + ':'
                        + date.getMinutes() + ':'
                        + date.getSeconds();
      console.log('datetime', publishDate);
      console.log('sid', this.sid);
      var that = this;
      var auth = localStorage.getItem('auth');
      var header ={'Authorization': auth};
      this.$http.post(this.url, {sid:that.sid, time:publishDate, content:that.userComment}, {credentials:true, headers:header}).then(response => {
        // success
        console.log('success', response);
        console.log('status', response.status);
        // pop event to father component
        that.$emit("childEvent", "success");
      }, response => {
        that.$emit("childEvent", response.status);
        console.log('failed');
      })
    }
  }
}
</script>

<style>
.comment{
  margin-top: 80px;
  display:flex;
}
.content{
  border: 1px solid #eff2f6;
  float: left;
  display: inline-block;
  width: 70%;
  text-align: left;
  border-radius: 15px;
  -moz-box-shadow: 10px 10px 5px #888888; 
  box-shadow: 10px 10px 5px #888888;
}
.avatar{
  border-radius: 10px;
  margin-left: 0px;
  padding:10px;
}
.uname{
  text-align: center;
  display: block;
}
.ubox{
  float:left;
  width: auto;
/*  align-items: left;
  text-align: left; */
}
.ctime{
  padding: 5px 10px;
  border-bottom: 1px dotted grey;
  display: block;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  background-color: #4396fd;/* #F5F8FA; */
  color:white;
}
#cbtn{
  margin-top: 20px;
}
</style>



