<template>
    <div id="menutab" ref="menuTab">
    <el-menu 
        :default-active="activeIndex" 
        class="el-menu-demo" 
        mode="horizontal" 
        background-color="#545c64"
        text-color="#ffffff"
        active-text-color="#ffd04b"
        >
    <el-menu-item index="1" @click="navigate('/')">Home</el-menu-item>
    <el-submenu index="3">
        <template slot="title">User</template>
        <el-menu-item index="3-1"  @click="navigate('/login')" id="loginbox">login/register</el-menu-item>
        <el-menu-item index="3-2"  @click="logout" >logout</el-menu-item>
        <el-menu-item index="3-3"  @click="navigate('/dashboard')" id="loginbox">dashboard</el-menu-item>
    </el-submenu>
    <el-submenu index="2">
        <template slot="title">Projects & Downloads</template>
        <el-menu-item index="2-1" @click="navigate('/doc/')">Sydney Documents</el-menu-item>
        <el-menu-item index="2-2" @click="navigate('/loststreet/')">The Lost Street</el-menu-item>
        <el-menu-item index="2-3" @click="navigate('/vr/')">Software Download</el-menu-item>
    </el-submenu>
    <el-menu-item @click="toProf" index="4">About Us</el-menu-item>
    </el-menu>
    <div class="line"></div>
    </div>
</template>

<script>
import Vue from 'vue'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import Cookies from 'js-cookie';

Vue.use(ElementUI)

export default {
  name: 'menutab',
  data() {
    return {
      ustate:false,
      activeIndex: '1',
      activeIndex2: '1'
    };
  },
  created: function () {
    var auth = localStorage.getItem('auth');
    if (auth)   this.ustate = true;
  },
  methods: {
    toProf: function(){
        window.location.href = "https://researchers.anu.edu.au/researchers/devine-k";
    },
    logout: function(){
        var token = Cookies.get('csrftoken');
        var header = {"Authorization":localStorage.getItem('auth'), "X-CSRFToken":token};
        this.$http.post('http://127.0.0.1:8000/usrinfo/usrLogout/',{},{headers:header,credentials:true}).then(response=>{
            localStorage.setItem('auth','');
            this.ustate = false;
            this.$router.push('/login');
        },response=>{
            console.log('failed to logout');
        })
    },
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
    navigate: function(r) {
      this.$router.push(r)
    },
  }
}
</script>

<styles>
#loginbox{
display:block;
}
</styles>



