<template>
  <div id="lsproject">
    <h1 style="padding-top:20px;">{{ban.name}}</h1>
    <div class="bblock">
        <a :href="ban.cover"><img class="banner" :src="ban.cover" /></a>
        <!--<span class="btitle" >{{ban.name}}</span>-->
        <p>{{ban.intro}}</p>
        <!-- carousel for illustrating the gallery image of this project -->
        <el-carousel v-if="gallery.length>0" :interval="4000" type="card"  height="200px">
            <el-carousel-item v-for="item in gallery" :key="item.index">
                <img class="picimg" :src="item.thumbnail" @click="toProject" :data-proid="item.id" />
            </el-carousel-item>
        </el-carousel>
        <!-- viewing the current unity file, may use the webGL -->

        <!-- upload button, first check user status -->

<!--        <el-button class="uploadBtn" @click="showUpWindow" type="primary">Upload Your Work<i class="el-icon-upload el-icon--right"></i></el-button>
        <transition name="el-fade-in-linear">
        <el-upload
        v-if="showUp"
        class="upload-demo"
        ref="upload"
        action="http://127.0.0.1:8000/"
        :on-preview="handlePreview"
        :on-remove="handleRemove"
        :file-list="fileList"
        :auto-upload="false">
        <el-button slot="trigger" size="small" type="primary">pick the file</el-button>
        <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">upload to server</el-button>
        <div slot="tip" class="el-upload__tip">jpg/png files only, no larger than 500kb</div>
        </el-upload>-->

        </transition>
        <!-- pinboard for comments-->

        <pinb @childEvent="childListener" :comments="comments" :url="'http://127.0.0.1:8000/loststreet/api_street_comment/'" :sid="1"></pinb>

    </div>
  </div>
</template>

<script>
import Vue from 'vue';
import pinboard from '@/components/pinboard'

Vue.component('pinb', pinboard)

export default {
  name: 'lsproject',
  data () {
    return {
    showUp: false,
    fileList: [{name: 'food.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}, {name: 'food2.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}],
      ban: {
        name:'', 
        cover:'',
        intro:'',
      },
      gallery: [],
      comments: [],
      sid:''
    }
  },
  methods: {
    childListener: function (data){
      console.log('child',data);
      if(data==='success'){
        this.$message({
            message:'post success',
            type:'success'}); 
      }else{
        if(data===401 || data===403) {
            this.$message.error('Please login first');
            // build a variable to navigate back directly later
            var status = 'redirect';
            this.$router.push({name:'login', params:{from:status}});
        }
        else this.$message.error('Post failed, please try agian');
      }
    },
    toProject: function () {
      this.inProject = true;
      this.$router.push('/loststreet/project');
    },
    submitUpload() {
      this.$refs.upload.submit();
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    showUpWindow: function () {
      this.showUp = !this.showUp;
    }
  },
  beforeCreate: function (){
    var head = 'http://127.0.0.1:8000';
    var sid =  this.$route.params.proid || 1; // get the sid from previous page
    this.sid = sid;
    var url = '/loststreet/api_street_page/';
    url = head + url;
    this.$http.get(url ,{params:{sid:sid}}).then(response=>{
        // success request
        var data = response.body;
        data.cover = head + data.cover;
        for (var x of data.gallery) {
            x.thumbnail = head + x.thumbnail;
            x.image = head + x.image;
        }        
        this.sid = data.id;
        this.ban = data;
        this.gallery = data.gallery;
        this.comments = data.comments;
        
        console.log('success',this.ban);
    }, response => {
        // fail
        console.log('failed') 
    })
  }
}
</script>

<style>
@import url(http://fonts.font.im/css?family=Cardo:400italic);
h1 {
  font-family: 'Cardo', serif;
}
.picimg{
    height:200px;
    width:200px;
}
.btitle{
  display: block;
  font-size:30px;
  font-weight: bold;
  font-family: 'Cardo', serif;
}
.bblock{
  padding:3%;
  background-color:white;
}
.banner{
  border-radius:15px;
  width:90%;
  height:270px;
}
.uploadBtn{
  margin-top: 20px;
  margin-bottom: 20px;
}
</style>



