<template>
  <div id="usr_settings">
    <h1 style="text-align:left;margin-left:15px;"> reset your password</h1>
    <el-form ref="form" :model="form" :rules="rules" class="form_panel" status-icon label-width="80px">
      <el-form-item label="old password" prop="opassword">
        <el-input type="password" v-model="form.opassword" ></el-input>
      </el-form-item>
      <el-form-item label="new password" prop="npassword">
        <el-input type="password" v-model="form.npassword"></el-input>
      </el-form-item>
      <el-form-item label="repeat" prop="rpassword">
        <el-input type="password" v-model="form.rpassword"></el-input>
      </el-form-item>
      <el-button type="primary" @click="resetPass">Update</el-button>
    </el-form>
  </div>
</template>

<script>
import Vue from 'vue'
let Base64 = require('js-base64').Base64;

export default {
  name:'profile',
  data () {
    var checkOldPass =  (rule, value, callback) => {
        console.log('occ');
        if (value==='') callback(new Error('Please input the password'));
        else callback();
    };
    var checkNewPass = (rule, value, callback) => {
        console.log('cc');
        if (value==='') callback(new Error('Please input the password'));
        else callback();
    };
    var checkRePass = (rule, value, callback) => {
        console.log('cc2')
        if (value==='') callback(new Error('Please input the password'));
        else if (value!==this.form.npassword)   callback(new Error('Passwords are not the same'))
        else callback();
    };
    return {
      form:{
        opassword:'',
        npassword:'',
        rpassword:''
      },
      rules:{
        opassword:[{validator: checkOldPass, trigger:'blur'}],
        npassword:[{validator: checkNewPass, trigger:'blur'}],
        rpassword:[{validator: checkRePass, trigger:'blur'}]
      }
    }
  },
  methods:{
    resetPass: function (){
      var that = this;
      var npass = this.form.npassword, opass = this.form.opassword;
      var head = 'http://127.0.0.1:8000';
      var url = head + '/usrinfo/resetPass/';
      var auth = localStorage.getItem('auth');
      var header = {'Authorization':auth};
      if (!auth){
        this.error.$message('please login first');
        this.$router.push('/login');
      }
      this.$refs['form'].validate((valid)=>{
        if (valid){
            that.$http.post(url, {npass:npass,opass:opass},{headers:header}).then(response=>{
                // request success
                console.log('reset success');
                that.$message({
                    message:'Password reset success!',
                    type:'success'
                });
                // renew the auth
                var uname = response.body;
                var newauth = 'Basic ' + Base64.encode(uname+':'+npass); 
                localStorage.setItem('auth', newauth);
                that.$router.push('/dashboard/')
            }, response=>{
                console.log('reset failed');
                // reqeust failed
                that.$message.error('something goes wrong, please try again')
            })
        }else console.log('invalid form');
      })
    }
  }
}
</script>

<style>
#usr_settings{
  float:left;
  margin-top:1%;
  margin-left:5%;
  width:70%;
  background-color:white;
}
</style>
<style >
.form_panel{
    margin:20px;
}
.el-form-item__label{
  line-height:20px;
}
</style>



