<template>
  <div id="doc_home">
    <div id="doc_title">
        <h1>Stories of Streets</h1> 
    </div>
    <div id="doc_body">
      <div class="bblock" @click="toStreet" v-for="ban in banners" :data-proid="ban.id">
        <img class="banner" :src="ban.cover" ></img>
        <span class="btitle" >{{ban.name}}</span>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
export default {
  name: 'doc_home',
  data () {
  return{
    banners: [],
    }
  },
  beforeCreate: function() {
    var head = 'http://127.0.0.1:8000';
    var url = head + '/doc/api_street_list/';
    var that = this;
    this.$http.get(url).then(response=>{
        // success request
        console.log('street list success');
        response.body.forEach(function(e){
            e.cover = head + e.cover;
        });
        this.banners = response.body;
    }, response=>{
        console.log('street list failed', response);
    })
  },
  methods: {
    toStreet: function (event) {
      var sid = event.currentTarget.getAttribute("data-proid");
      console.log('sid',sid);
      this.$router.push({name:'doc_street', params:{sid:sid}});
    }
  }
}
</script>

<style scoped>
@import url(http://fonts.font.im/css?family=Cardo:400italic);

.btitle{
  display: block;
  font-size:30px;
  font-weight: bold;
  font-family: 'Cardo', serif;
  color:grey;
}
.bblock{
  padding:3%;
  }
.banner{
  border-radius:15px;
  width:90%;
  height:270px;
}
#doc_main{
  margin-left:20%;
  width:60%;
}
#doc_title{
  padding:30px; 
}
</style>



