<template>
    <div id="doc_edit">
      <editor @childEvent="childListener"></editor>
    </div>
</template>

<script>
import Vue from 'vue'
import editor from '@/components/editor' 

Vue.component('editor', editor)
export default {
  name:'doc_edit',
  methods:{
    childListener: function (data) {
        if(data.status==='success'){
            this.$message({
                message: 'submit success! Your contribution will be displayed after inspection.',
                type:'success'
            });
            this.$router.push({name:'doc_building', params:{bid:data.bid}});
        }else if (data.status===401 || data.status===403){
            this.$message.error('Please login first');
            // build a variable to navigate back directly later
            var status = 'redirect';
            this.$router.push({name:'login', params:{from:status}});
        }else{
            this.$message.error('Post failed, please try again');
        }
    }
  }
}
</script>

<style>
editor{
  width:100%;
  margin-left:0;
  opacity:1;
}
editor{
  box-sizing:border-box;
}
</style>



