<template>
  <div id="doc_building">
    <div v-html="building.intro" id="htmlContainer"> </div>
    <div id="floatWin">
      <ul>
        <li @click="goEdit">Edit this page</li>
        <li @click="goStreet">Back to Street Page</li>
        <li>Report this page</li>
      </ul>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'

export default {
  name:'doc_building',
  data () {
    return {
      building:{},
    }
  },
  beforeCreate: function (){
    var bid = this.$route.params.bid || -1;
    var head = 'http://127.0.0.1:8000';
    var url = head + '/doc/api_building_info/';
    if (bid===-1){
        // failed to get the params
        this.$message.error('An unknown error ocurred, please try again');
        this.$router.go(-1);
    }
    this.$http.get(url, {params:{bid:bid}}).then(response =>{
        // request success
        this.building = response.data;
    }, response=>{
        console.log('failed to request');
    });
  },
  methods: {
    goEdit: function () {
      localStorage.setItem('building', JSON.stringify(this.building));
      this.$router.push('/doc/edit');
    },
    goStreet: function(){
      var sid = this.building.stname;
      this.$router.push({name:'doc_street', params:{sid:sid}});
    }
  }
}
</script>

<style>
#doc_building{
  height:100%;
/*  opacity:1;*/
  background-color:white;
}
#htmlContainer{
  text-align:left;
  padding-top:10px;
}
#htmlContainer p {
  padding:5px;
}
#htmlContainer h3 {
  padding-left:5px;
}
#floatWin{
  padding:10px;
  padding-right:20px;
  position:fixed;
  left:1%;
  top:20%;
  background-color:black;
  color:white;
  opacity:0.9;
  border-left: red 2px solid;
}
#floatWin li{
  text-align:left;
  padding-bottom:10px;
  text-decoration:underline;
}
</style>



