<template>
    <div id="login">
        <template v-if="formStatus === 'login'" >
        <el-form ref="form" :model="lform">
            <el-form-item label="username" >
                <el-input v-model="lform.username"></el-input>
            </el-form-item>
            <el-form-item label="password">
                <el-input type="password" v-model="lform.password"></el-input>
            </el-form-item>
            <el-form-item><el-button type="primary" @click="submit">Login</el-button></el-form-item>
            <el-form-item><el-button type="text" @click="swapcard" id="register">Register</el-button></el-form-item>
        </el-form>
        </template>
        <div v-else-if="formStatus === 'register'" >

        <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm"  class="demo-ruleForm">
            <el-form-item label="username" prop="uname">
                <el-input v-model="ruleForm.username"></el-input>
            </el-form-item>
            <el-form-item label="password" prop="pass">
                <el-input type="password" v-model="ruleForm.pass" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="repeat password" prop="checkPass">
                <el-input type="password" v-model="ruleForm.checkPass" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="register">Register</el-button>
            </el-form-item>
            <el-form-item><el-button type="text" @click="swapcard" id="register">sign in</el-button></el-form-item>
        </el-form>
        </div>
    </div>
</template>

<script>
import Vue from 'vue'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import VueResource from 'vue-resource'
let Base64 = require('js-base64').Base64;

Vue.use(VueResource)

export default {
  name: 'login',
  data () {
          var validatePass = (rule, value, callback) => {
                console.log('valipass', value);
                  if (value === '') {
                          callback(new Error('please input the password'));
                  }
                  else if (value.length<6) {
                        callback(new Error('the password is too short'));
                  } 
                  else {
                          callback();
                  }
          };
          var validatePass2 = (rule, value, callback) => {
            console.log('valipass2', value);
                  if (value === '') {
                          callback(new Error('please input the password again'));
                  } else if (value !== this.ruleForm.pass) {
                          callback(new Error('mismatch with the origin password'));
                  } else {
                          callback();
                  }
          };
    var checkuname = (rule, value, callback) => {
      console.log('valiuname', this.ruleForm.username);
      value = this.ruleForm.username;
      if(!value)
        return callback(new Error('username cannot be blank'));
      else
        return callback();
    };
    return {
      formStatus:'login',
      ruleForm:{
        username:'',
        pass:'',
        checkPass:''
      },
      rules:{
        uname: [{ validator: checkuname, trigger: 'blur' }],
        pass: [{ validator: validatePass, trigger: 'blur' }],
        checkPass: [{ validator: validatePass2, trigger: 'blur'}]
      },
      lform: {
        username:'',
        password:''
      },
    };
  },
  methods:{
    /* form submission */
    register: function(){
        if(this.ruleForm.pass!==this.ruleForm.checkPass)    this.$message.error('passwords should be same');
        else{
            var head = 'http://127.0.0.1:8000';
            var url = head + '/usrinfo/repeatUser/';
            var that = this;
            this.$http.get(url, {params:{username:that.ruleForm.username}}).then(response=>{
                // success
                console.log('res', response.body)
                if(response.body==='pass'){
                    url = head + '/usrinfo/usrRegister/';
                    that.$http.post(url, {username:that.ruleForm.username, password:that.ruleForm.pass}).then(response=>{
                        // success
                        that.$message({message:'register success', type:'success'});
                        that.swapcard();
                    }, response=>{
                        console.log('failed', response);
                        that.$message.error('register failed, please try later');
                    })
                }
                else
                    that.$message.error('username already exists, please change to another one');
            }, response=>{})

        }
    },
    /* to validate with backend */
    submit: function() {
      console.log('submitted');
      var that = this;
      var previousStatus = this.$route.params.from;
      console.log('previous status', previousStatus);
      this.$http.post('http://127.0.0.1:8000/usrinfo/authValidate/',{username:this.lform.username,password:this.lform.password},{credentials:false}).then(response =>{
        // sucess for this 
          var sd = response.body;
          console.log('success, get response:', sd);
          var auth = Base64.encode(that.lform.username + ':' + that.lform.password);
          auth = 'Basic ' + auth;
          console.log('base64', auth);
          localStorage.setItem('auth', auth); 
          // if the user comes from other page that need authentication operation
          if (previousStatus)   that.$router.go(-1);
          else this.$router.push('/dashboard'); // else go to dashboard by default
        }, response => {
          console.log('there is an error', response);
          this.$message.error('username or password is incorrect');
      });
      console.log('submit!');
    },
    swapcard: function () {
      this.formStatus = this.formStatus==='register'?'login':'register';
    }
  }
}
</script>

<style>
#login{
  border-radius:10px;
  background-color:white;
  opacity:0.95;
  margin-left:25%;
  margin-top:10%;
  width:33%;
  padding:5%;
}
#register{
position:absolute;
right:5px;
bottom:5px;
}
</style>



