<template>
  <div id="doc_street">
    <!-- banner for the street --> 
    <div>
      <label id="str_name">{{str_name}}</label>
      <img :src="banner" id="str_banner" />
    </div>
    <!-- introduction to the street -->
    <p class="intro" v-html="intro"></p>
    <!-- image navigators to buildings-->
    <div class="bnav" v-for="item in buildings" :key="item.index" :data-bid="item.id" @click="toBuilding">
      <div class="bname"><label >{{item.name}}</label><i class="el-icon-caret-right"></i></div>
      <img class="bpic" :src="item.cover" height="200px" />
    </div>
  </div>
</template>

<script>
import Vue from 'vue'

export default {
  name:'doc_street',
  data () {
    return {
      banner:'', 
      str_name: '',
      intro:'',
      buildings: []
    }
  },
  beforeCreate: function () {
    var sid = this.$route.params.sid || 1;
    var head = 'http://127.0.0.1:8000';
    var url = head + '/doc/api_street_info/';
    this.$http.get(url, {params:{sid:sid}}).then(response=>{
        // request success
        console.log('request success');
        var data = response.body;
        this.str_name = data.name;
        this.banner = head + data.cover;
        this.intro = data.intro;
        data.buildings.forEach(function(e) {
            e.cover = head + e.cover;
        });
        this.buildings = data.buildings;
    }, response=>{
        console.log('request failed');
    })
  
  },
  methods: {
    toBuilding: function (event) {
      var bid = event.currentTarget.getAttribute("data-bid");
      console.log('bid', bid);
      this.$router.push({name:'doc_building', params:{bid:bid}});
    }
  },
}
</script>

<style>
/*
@import url(http://fonts.font.im/css?family=Cardo:400italic);
@import url(http://fonts.font.im/css?family=Dancing+Script:700);
*/
#doc_street{
  height:100%;
  opacity:0.9;
  background-color:white;
}
#str_banner{
  width:100%;
  height:300px;
  -webkit-mask-image:-webkit-radial-gradient(0% 100%, rgba(0,0,0,0),rgba(0,0,0,0.8));
}
.bname{
  padding-left:10px;
  text-align: left;
  font-size:1.5em;
  margin-bottom:20px;
  display:inline-block;
  font-weight:bold;
}
#str_name{
  position:absolute;
  margin-bottom:0px;
  margin-left:10px;
  font-size:5em;
  font-weight:bolder;
}
.intro:first-letter{
  text-indent: 10px;
  font-weight: bold;
  font-size: 3em;
  /* font-family: 'Cardo', serif; */
}
.intro{
  text-align:left;
  padding:10px;
  font-size:1.3em;
  /*font-family: 'Dancing Script', cursive;*/
}
.bnav{
  padding-top:20px;
  margin-bottom:20px;
}
.bpic {
  width:100%;
}
.bname{
}
</style>



