<template>
  <div id="usr_profile">
    <el-form ref="form" :model="form" class="form_panel" label-width="80px">
<!-- Decide not having avatar currently -->
<!--        <el-form-item label="avatar" >                            
              <el-upload
                class="upload-demo"
                action=""
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :file-list="fileList2"
                list-type="picture">
               <el-button size="small" type="primary">Choose the picture</el-button>
              <div slot="tip" class="el-upload__tip">jpg/png onlyno larger than 500kb</div>
              </el-upload>
         </el-form-item> -->
      <el-form-item label="username">
        <el-input v-model="form.username"></el-input>
      </el-form-item>
      <el-form-item label="organization">
        <el-input v-model="form.organization"></el-input>
      </el-form-item>
      <el-form-item label="email">
        <el-input v-model="form.email"></el-input>
      </el-form-item>
      <el-form-item label="summary">
        <el-input 
          type="textarea" 
          v-model="form.summary"
          :autosize="{minRows:2}"></el-input>
      </el-form-item>
      <el-button type="primary" @click="updateProfile">Update</el-button>
    </el-form>
  </div>
</template>

<script>
import Vue from 'vue'

export default {
  name:'profile',
  data () {
    return {
      fileList2: [{name: 'food.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}],              
      imageUrl:'',
      form:{
        avatar:'',
        username:'',
        organization:'',
        email:'',
        summary:''
      }
    }
  },
    // since need to call functions in `methods`, have to
    // wait until the methods are ready
  beforeMount: function (){
    var header = this.getAuth();
    var head = 'http://127.0.0.1:8000';
    var url  = head + '/usrinfo/basicInfo/';
    var that = this;
    this.$http.get(url, {credentials:true, headers:header}).then(response=>{
        // request success
        var data = response.data;
        that.form.avatar = data.avatar || '';
        that.form.username = data.user.username;
        that.form.email = data.user.email;
        that.form.summary = data.summary || '';
        that.form.organization = data.organization || '';
        console.log('request success');
    }, response=>{
        if (response.status===401 || response.status===403)
            that.$message.error('Plase login first');
        console.log('request failed');
        this.$router.push('/login');
    });
  },
  methods:{
      getAuth: function(){
        var auth = localStorage.getItem('auth');
        if(!auth){
            this.$message.error('Session expired, Please login first');
            this.$router.push('/login');
            return 'unauth';
        }
        var header = {'Authorization': auth};
        return header;
      },
      updateProfile: function () {
        var organization = this.form.organization;
        var summary = this.form.summary;
        var email = this.form.email;
        var params = {
            organization: organization, 
            summary:summary, 
            email:email        
        };
        // Get the authorization info
        var header = this.getAuth();
        if(header!=='unauth'){  // avoid posting rubbish
        var head = 'http://127.0.0.1:8000';
        var url = head + '/usrinfo/basicInfo/';
        this.$http.post(url, params, {headers:header}).then(response=>{
            // request success
            this.$message({
                message:'Profile Updated',
                type:'success'
            });    
        }, response=>{
            this.$message.error('We encountered some errors, please try again');
        })
        }
      },
      handleRemove(file, fileList) {
              console.log(file, fileList);
      },
      handlePreview(file) {
              console.log(file);
      }
  }
}
</script>

<style>
#usr_profile{
  float:left;
  margin-top:1%;
  margin-left:5%;
  width:70%;
  background-color:white;
}
.panel-box{
  display:inline-flex;
}
.input_box{
  width:300px;
  margin-left:10px;
}
.form_panel{
  padding:10px;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409EFF;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.form_avatar, .avatar {
  width: 178px;
  height: 178px;
}

</style>



